import styled, { css } from 'styled-components'
import { fontStyleMapping } from '../../Text/fontMapping'
import { theme } from '../../theme'
import { TableStylesProps } from '../types'

export const StyledTable = styled.table<TableStylesProps>`
  width: 100%;
  height: 100%;
  border-collapse: collapse;
  overflow: auto;
  background: ${theme.colors.coconut};
  border-spacing: 30px;
`

export const StyledHeaderCell = styled.th<TableStylesProps>`
  background: ${theme.colors.coconut};
  position: ${({ fixedHeader }) => (fixedHeader ? 'sticky' : 'auto')};
  top: 0;
  z-index: 2;
  text-align: left;
  vertical-align: bottom;
  ${fontStyleMapping['label']};
  padding-left: 8px;
  padding-right: 8px;
  padding-top: 8px;
  padding-bottom: 8px;

  ${({ headerColor }) =>
    headerColor &&
    css`
      background: ${theme.colors[headerColor]};
    `}

  ${({ headerHeight }) =>
    headerHeight &&
    css`
      height: ${headerHeight};
    `}

  ${({ maxWidth }) =>
    maxWidth &&
    css`
      max-width: ${maxWidth};
    `}

  ${({ minWidth }) =>
    minWidth &&
    css`
      min-width: ${minWidth};
    `}
`

export const StyledCell = styled.td<TableStylesProps>`
  vertical-align: middle;
  overflow: hidden;
  padding-left: 8px;
  padding-right: 8px;
  padding-top: 8px;
  padding-bottom: 8px;

  ${({ noWrapContent }) =>
    noWrapContent &&
    css`
      white-space: nowrap;
    `};

  ${({ truncateContent }) =>
    truncateContent &&
    css`
      white-space: nowrap;
      text-overflow: ellipsis;
    `};

  ${({ stickyCell }) =>
    stickyCell &&
    css`
      position: sticky;
      right: 0;
    `};

  ${({ rowPadding }) =>
    rowPadding &&
    css`
      padding-top: ${rowPadding};
      padding-bottom: ${rowPadding};
    `};

  ${({ maxWidth }) =>
    maxWidth &&
    css`
      max-width: ${maxWidth};
    `}
`

export const StyledRow = styled.tr<TableStylesProps>`
  background: ${theme.colors.custard};

  ${({ rowBorderColor }) =>
    rowBorderColor &&
    css`
      border-bottom: 1px solid ${theme.colors[rowBorderColor]};
    `}

  ${({ rowColor }) =>
    rowColor &&
    css`
      background: ${theme.colors[rowColor]};
    `}

  ${({ stripedColor }) =>
    stripedColor &&
    css`
      &:nth-child(even) {
        background: ${theme.colors[stripedColor]};
      }
    `}
`
